$loader-height: 64px;

.loader {
  position: relative;
  margin: 15px auto;
  width: $loader-height;
  height: $loader-height;

  &:after {
    content: " ";
    display: block;
    margin: floor($loader-height / 2) auto;
    font-size: floor($loader-height / 8);
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: absolute;
    top: floor(-1 * $loader-height / 16);
    left: floor($loader-height / 2 - $loader-height / 16);
    text-indent: -9999em;
    -webkit-animation: load5 1.1s infinite ease;
    animation: load5 1.1s infinite ease;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
  }
}

